body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { 
  background-color: white;
}

.app-container {
  display:flex;
  flex-direction: row;
}


.chat {
  width: 100%;
  margin-left: auto; 
  margin-right: auto;
  font-family:tahoma; 
  font-size:12px; 
  padding: 10px;
}

.chat .content { 
  padding: 15px 15px 25px 15px;
  border-radius:5px;
  overflow-y: scroll; 
  border: 1px solid #CCC;
  margin-top: 10px; 
  height: 160px; 
}

.chat .content .content-message {
  line-height: 20px;
  margin: 0;
}

.chat .chat-input {
  border-radius:2px; 
  border: 1px solid #ccc;
  margin-top: 10px; 
  padding: 5px; 
  width: 100%;
}

.chat .chat-status {
  display:block;
  float:left;
  margin-top:15px; 
  margin-right: 5px;
}

.map-container {
  width: 800px;
  background-color: black;
}
